import React from 'react'

interface FeedbackContext {
    feedbackShow: boolean
    setFeedbackShow: (show: boolean) => void
    sendFeedbackNotificationRef: React.MutableRefObject<() => void>
}

export const FeedbackContext = React.createContext<FeedbackContext>({
    feedbackShow: false,
    setFeedbackShow: () => {},
    sendFeedbackNotificationRef: {current: () => {}}
})

export const useFeedbackContext = () => React.useContext(FeedbackContext)
