import React, { useState } from 'react'
import { CardSelector } from './card-selector'
import { Card } from '../../../types/card'
import { CardTryTable } from './card-try-table'
import { useTranslation } from 'react-i18next'
import { snapdleNameSpace } from '../../../i18n/resources'

import { getAllCards, getCardOfTheDay, getUpdateDate, whenIsNextCardOfTheDay } from './card-manager'
import Countdown from 'react-countdown'
import { useUserContext } from '../../user/user.contex'
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useFeedbackContext } from '../../feedback/feedback.context'
import { ClassicTutorialButton } from './tutorial'
import { ClassicShareResultButton } from './share-result'
import { GameContainer } from '../core/game-container'


export const ClassicGame: React.FC = () => {
    const { t } = useTranslation(snapdleNameSpace)
    const { sendFeedbackNotificationRef } = useFeedbackContext()
    const cards = getAllCards()

    const gameController = useUserContext().getClassicGameController()
    const isFirstGame = useUserContext().user?.gameData?.classic.statistics.totalGames == 0
    const status = gameController ? gameController?.getGameStatus() : null
    const [loaded, setLoaded] = useState(false)


    const [cardOfTheDay, setCardOfTheDay] = useState<any>(getCardOfTheDay(cards))
    const [nextCard, setNextCard] = useState(whenIsNextCardOfTheDay())
    const [cardTries, setCardTries] = useState<Card[]>([])
    const [win, setWin] = useState(status?.win || false)

    React.useEffect(() => {
        if (gameController && status && !loaded) {
            setLoaded(true)
            setCardTries(status.getTriesOfUser(cards))
            setWin(status.win)
        }
    }, [status, loaded])

    const selectCard = (card: any) => {
        if (cardTries.includes(card)) {
            toast(t('game.classic.cardAlreadySelected'))
            return
        } else if (card == cardOfTheDay) {
            if (isFirstGame) {
                sendFeedbackNotificationRef.current()
            }
            setCardTries([card, ...cardTries])
            setWin(true)
            gameController?.addTryAndWin(card.id)
        } else {
            setCardTries([card, ...cardTries])
            gameController?.addTry(card.id)
        }
    }

    const reloadCardOfTheDay = () => {
        if (loaded) {
            setNextCard(whenIsNextCardOfTheDay())
            setCardOfTheDay(getCardOfTheDay(cards))
            setLoaded(false)
            gameController?.nextDay()
        }
    }

    const selectableCards = cards.filter(card => !cardTries.includes(card))

    return <GameContainer loaded={loaded}>
        <div className="row justify-content-md-center text-center">
            <div className="gameCommander-container col col-lg-4">
                <div className="row">
                    <div className='col '>
                        {!win ?
                            <>
                                <h4 className='marvel-text'>{t('game.classic.descriptionTitle')}</h4>
                                <h6 className='marvel-text'>{t('game.classic.description')}</h6>
                                <CardSelector cards={selectableCards} onChange={selectCard}></CardSelector>
                                <div className='marvel-text version-message mt-2'>
                                    {t('common.updateDate') + ' ' + (new Date(getUpdateDate()).toLocaleDateString())}
                                </div>
                            </>
                            :
                            <div className='marvel-text'>
                                <h1 className='win-message'>{t('game.classic.win')}</h1>
                                <ClassicShareResultButton cardOfTheDay={cardOfTheDay} cardTries={cardTries} />
                                <Countdown date={nextCard}
                                    onComplete={reloadCardOfTheDay}
                                    renderer={({ days, hours, minutes, seconds, completed }) => {
                                        if (completed) {
                                            // Render a completed state
                                            return <></>;
                                        } else {
                                            // Render a countdown
                                            return <>
                                                <div className="row">
                                                    <h3>{t('game.classic.timeToNextCard')}</h3>
                                                </div>
                                                <div className="row">
                                                    <h3>{hours < 10 ? '0' : ''}{hours}:{minutes < 10 ? '0' : ''}{minutes}:{seconds < 10 ? '0' : ''}{seconds}</h3>
                                                </div>
                                            </>;
                                        }
                                    }} />
                            </div>
                        }
                    </div>
                </div>
                <br />
                {!win && <ClassicTutorialButton />}
            </div>
        </div>
        <CardTryTable cards={cardTries} cardOfTheDay={cardOfTheDay}></CardTryTable>
    </GameContainer>
}