import React, { MouseEventHandler } from "react";
import '../../resources/styles/social-media.css'
import GitHubLogo from '../../resources/images/rrss/GitHub.png'
import KoFiLogo from '../../resources/images/rrss/kofi.png'
import Feedback from "../../resources/images/rrss/feedback.jpeg";
import { useFeedbackContext } from "../feedback/feedback.context";
import '../../resources/styles/bubbles.css'
import { useTranslation } from "react-i18next";
import { snapdleNameSpace } from "../../i18n/resources";
import { animated, useSpring } from "@react-spring/web";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { useGAController } from "../utils/analytics";

export const IconUrl: React.FC<{ logo: string, url: string, onClick?: MouseEventHandler<HTMLAnchorElement> }>
    = ({ logo, url, onClick }) => {
        return <a href={url} target="_blank" rel="noreferrer" onClick={onClick}>
            <img src={logo} className="rrss-icon"></img>
        </a>
    }

const FeedbackIcon: React.FC<{ onClick?: MouseEventHandler<HTMLImageElement> }> = ({ onClick }) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { setFeedbackShow, sendFeedbackNotificationRef } = useFeedbackContext()
    const [feedbackBubbleShow, setFeedbackBubbleShow] = React.useState(false)

    


    const [props, api] = useSpring(() => ({ opacity: 0, scale: 1.01 }))

    const showBubble = () => {
        setFeedbackBubbleShow(true)
        api.start({
            from: {
                opacity: 0,
                scale: 1.01
            },
            to: [{
                opacity: 1.01,
            },
            {
                scale: 1.1,
            },
            {
                scale: 1.01,
            },
            {
                scale: 1.1,
            },
            {
                scale: 1.01,
            }]
        })
    }
    sendFeedbackNotificationRef.current = showBubble

    const hideBubble = () => {
        api.start({
            from: {
                opacity: 1
            },
            to: [{
                opacity: 0,
            }],
            onResolve: () => setFeedbackBubbleShow(false)
        })
    }

    return <>
        {feedbackBubbleShow &&
            <animated.div style={{ ...props, scale: (props.scale == null ? '1' : props.scale) }}>
                <div className="speech-bubble">
                    <div className="close-bubble-button" onClick={() => hideBubble()}>X</div>
                    <div className="title">{t('rrss.sendMeFeedback')}</div>
                </div>
            </animated.div>
        }

        <img src={Feedback} className="rrss-icon" onClick={(e) => { onClick && onClick(e); setFeedbackShow(true) }}></img>
    </>
}

export const OverlayTooltip: React.FC<{ text: string, children: React.ReactNode }> = ({ text, children }) => {
    const tooltip = (<Tooltip>{text}</Tooltip>);
    return <OverlayTrigger placement="top" overlay={tooltip}>
        <div className="rrss-icon-container">{children}</div>
    </OverlayTrigger>
}
export const SocialMediaBar: React.FC<{}> = () => {
    const { t } = useTranslation(snapdleNameSpace)
    const { doInteraction } = useGAController('rrss')

    return <>
        <div className="rrss-flex-container"><div className="rrss-container">
            <OverlayTooltip text={t('rrss.icon.feedback')}><FeedbackIcon onClick={() => doInteraction('FeedbackClick')} /></OverlayTooltip>
            <OverlayTooltip text={t('rrss.icon.github')}><IconUrl logo={GitHubLogo} url="https://github.com/khmDEV" onClick={() => doInteraction('GithubClick')}></IconUrl></OverlayTooltip>
            <OverlayTooltip text={t('rrss.icon.kofi')}><IconUrl logo={KoFiLogo} url="https://ko-fi.com/khmdev" onClick={() => doInteraction('KofiClick')}></IconUrl></OverlayTooltip>
        </div>
        </div></>;
}