import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { snapdleNameSpace } from '../../../i18n/resources'

import Countdown from 'react-countdown'
import { useUserContext } from '../../user/user.contex'
import { toast } from 'react-toastify';
import '../../../resources/styles/stats.css'
import { useFeedbackContext } from '../../feedback/feedback.context'
import { ClassicTutorialButton } from './tutorial'

import { GameContainer } from '../core/game-container'
import { StatsCardSelector } from './card-selector'
import { StatsCards, StatsRange } from '../../../types/stats'
import { StatsGameProvider } from './stats-game.provider'
import { CardStatsManager, getCardStatsManager, rangeImages } from './card-manager'


export const StatsGame: React.FC = () => {
    return <StatsGameProvider>
        <StatsContainerGame />
    </StatsGameProvider>
}


const StatsContainerGame: React.FC = () => {
    const [range, setRange] = useState<StatsRange | undefined>(undefined)
    const [currentCardStatsManager, setCurrentCardStatsManager] = useState<CardStatsManager | undefined>(undefined)


    React.useEffect(() => {
        const manager = getCardStatsManager(range)
        setCurrentCardStatsManager(manager)
    }, [range])


    const doBack = () => {
        setRange(undefined)
    }

    return <StatsGameProvider>
        <GameContainer extraClasses={'gameStats-container'}>
            <div className="row justify-content-md-center text-center">
                <div className="gameCommander-container gameStatsCommander-container col col-lg-4">
                    {currentCardStatsManager && currentCardStatsManager.getRange() ?
                        <StartedGame doBack={doBack} cardStatsManager={currentCardStatsManager} /> :
                        <StatsSelectorGame changeRange={setRange} />}

                </div>
            </div>
        </GameContainer>
    </StatsGameProvider>
}
const StatsSelectorGame: React.FC<{ changeRange: (range: StatsRange) => void }> = ({ changeRange }) => {
    return <>
        <h1>Select a range</h1>
        {Object.keys(rangeImages).map((keyCode) => {
            const key = keyCode as StatsRange;

            return <><img key={key} src={((rangeImages as any)[key] as any)} onClick={() => {
                changeRange(key)
            }} style={{
                cursor: 'pointer'
            }} />
                <br />
            </>
        })}
    </>
}

const StartedGame: React.FC<{ doBack: () => void, cardStatsManager: CardStatsManager }> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { doBack, cardStatsManager } = props
    const { sendFeedbackNotificationRef } = useFeedbackContext()

    const gameController = useUserContext().getStatsGameController()
    const isFirstGame = useUserContext().user?.gameData?.classic.statistics.totalGames == 0
    const status = gameController ? gameController?.getGameStatus() : null
    const [loaded, setLoaded] = useState(false)

    const [statsCards, setStatsOfTheDay] = useState(cardStatsManager.getStatsOfTheDay())

    const [nextCard, setNextCard] = useState(cardStatsManager.whenIsNextCardOfTheDay())
    const [cardTries, setCardTries] = useState<StatsCards[]>([])
    const [win, setWin] = useState(status?.win || false)


    React.useEffect(() => {
        if (gameController && status && !loaded) {
            setLoaded(true)
            setCardTries(status.getTriesOfUser())
            setWin(status.win)
        }
    }, [status, loaded])

    React.useEffect(() => {
        console.log('Stats of the day', cardStatsManager.getStatsOfTheDay())
        setStatsOfTheDay(cardStatsManager.getStatsOfTheDay())
    }, [cardStatsManager])

    const selectCard = (card: any) => {
        if (cardTries.includes(card)) {
            toast(t('game.classic.cardAlreadySelected'))
            return
        } else if (card == statsCards) {
            if (isFirstGame) {
                sendFeedbackNotificationRef.current()
            }
            setCardTries([card, ...cardTries])
            setWin(true)
            //gameController?.addTryAndWin(card.id)
        } else {
            setCardTries([card, ...cardTries])
            //gameController?.addTry(card.id)
        }
    }

    const reloadCardOfTheDay = () => {
        if (loaded) {
            setNextCard(cardStatsManager.whenIsNextCardOfTheDay())
            setStatsOfTheDay(cardStatsManager.getStatsOfTheDay())
            setLoaded(false)
            gameController?.nextDay()
        }
    }


    return <>
        <div className='row'>

            {!win && <div className='col'><ClassicTutorialButton /></div>}
            <div className='col'>
                <button onClick={doBack} className='btn btn-primary marvel-text'>Return</button>
            </div>
        </div>
        <br />
        <div className="row">
            <div className='col '>
                {!win ?
                    <StatsCardSelector onSend={() => { }} stats={statsCards} />
                    :
                    <div className='marvel-text'>
                        <h1 className='win-message'>{t('game.classic.win')}</h1>

                        <Countdown date={nextCard}
                            onComplete={reloadCardOfTheDay}
                            renderer={({ days, hours, minutes, seconds, completed }) => {
                                if (completed) {
                                    // Render a completed state
                                    return <></>;
                                } else {
                                    // Render a countdown
                                    return <>
                                        <div className="row">
                                            <h3>{t('game.classic.timeToNextCard')}</h3>
                                        </div>
                                        <div className="row">
                                            <h3>{hours < 10 ? '0' : ''}{hours}:{minutes < 10 ? '0' : ''}{minutes}:{seconds < 10 ? '0' : ''}{seconds}</h3>
                                        </div>
                                    </>;
                                }
                            }} />
                    </div>
                }
            </div>
        </div>
        <br />
    </>

}