import { getToday } from "../components/user/utils";
import { StatsCards } from "../types/stats";
import { ClassicGameData, GameData, User, StatsGameData } from "../types/user";
import { v4 as uuidv4 } from 'uuid';

const initGameData: () => GameData = () => {
    return {
        currentGame: {
            date: getToday(),
            win: false
        },
        gameHistory: [],
        statistics: {
            totalGames: 0,
            totalWins: 0,
            currentStreak: 0,
            longestStreak: 0,
            averageTries: 0
        }
    }
}

const initClassicGameData: () => ClassicGameData = () => {
    const data = initGameData();
    return {...data, currentGame: {cardTries: [], ...data.currentGame}}
}
const initStatsGameData: () => StatsGameData = () => {
    const data = initGameData();
    return {...data, currentGame: {cardTries: [] as StatsCards[], ...data.currentGame}}
}

const initUser: () => User = () => {
    return {
        id: uuidv4(),
        language: undefined,
        gameData: {
            classic: initClassicGameData(),
            stats: initStatsGameData()
        }
    }
}

const fixUserData: (user: User) => User = (user) => {
    if (user == null) {
        user = initUser();
    } if (user.id == null) {
        user.id = uuidv4();
    } else if (user.gameData && user.gameData?.classic == null) {
        user.gameData.classic = initClassicGameData()
    } else if (user.gameData == null) {
        user.gameData = {
            classic: initClassicGameData(),
            stats: initStatsGameData()
        }
    }
    return user;
}

export const getUser: () => User = () => {
    const item = window.sessionStorage.getItem("user");
    const userData = item != null ? JSON.parse(item) : undefined;

    return fixUserData(userData);
};

export const updateUser: (user: User) => void = (user) => {
    window.sessionStorage.setItem("user", JSON.stringify(user));
}