import type { CSSProperties, FC, ReactNode } from 'react'
import React from 'react'
import { DragSourceMonitor, useDrop } from 'react-dnd'
import { Card } from './card'
import { Stats } from '../../../../types/stats'

const style: CSSProperties = {
  height: '12rem',
  width: '12rem',
  marginRight: '1.5rem',
  marginBottom: '1.5rem',
  color: 'white',
  padding: '1rem',
  textAlign: 'center',
  fontSize: '1rem',
  lineHeight: 'normal',
  display: 'inline-block',
}
const styleContainer: CSSProperties = {
  height: 'max-content',
  width:  'max-content',
  textAlign: 'center',
  fontSize: '1rem',
  lineHeight: 'normal',
  display: 'inline-block',
}

export interface CardHolderProps {
  stats: Stats
  accept: string[]
  card: string | null
  onDrop: (item: any) => void
  onDropCardRef?: React.MutableRefObject<(item: {name: string;}, element: DragSourceMonitor) => void>
  children?: ReactNode
}

export const CardHolder: FC<CardHolderProps> = ({
  accept,
  card,
  onDrop,
  onDropCardRef,
  stats
}) => {
  const [{ isOver, canDrop }, drop] = useDrop({
    accept,
    drop: onDrop,
    collect: (monitor) => ({
      isOver: monitor.isOver(),
      canDrop: monitor.canDrop(),
    }),
  })

  const isActive = isOver && canDrop
  let backgroundColor = '#222'
  if (isActive) {
    backgroundColor = 'darkgreen'
  } else if (canDrop) {
    backgroundColor = 'darkkhaki'
  }
  
  return (
    <div style={{ ...styleContainer }}  >
      <div ref={drop} style={{ ...style, backgroundColor }} data-testid="dustbin">
        {card ? <Card name={card} onDropRef={onDropCardRef}></Card> : <h1>?</h1>}
      </div>
      <div className='text-centered'>{stats.games??0} games</div>
      <div className='text-centered'>Winrate {stats.winrate??0}%</div>
      <div className='text-centered'>Avg. cubes {stats.avgCubes??0}</div>
      <div className='text-centered'>Winrate when play {stats.winPlay??0}%</div>
    </div>
  )
}
