import React from 'react'
import { ClassicGameData, ClassicGameStatus, GameData, GameStatus, User } from '../../types/user'
import { UserContext } from './user.contex'
import { getUser, updateUser } from '../../api/user'
import { Card } from '../../types/card'
import { getToday, sameDay } from './utils'
import { Language, getLanguageFromNavigator } from '../../i18n/i18n'
import ReactGA from 'react-ga4'


interface UserProviderProps {
    children: React.ReactNode
}

export const generateClassicGameController = (user: User, gameData: ClassicGameData, setUser: (user: User) => void) => {

    const gameName = 'classic'

    if (user == null) {
        return undefined;
    }

    const addTry = (cardId: string) => {
        gameData.currentGame.cardTries.push(cardId)
        ReactGA.event('Try',{
            category: gameName,
            user: user.id,
            try: cardId,
            tries: gameData.currentGame.cardTries.length.toString()
        });
        setUser(user)
    }
    const addTryAndWin = (cardId: string) => {
        addTry(cardId)

        ReactGA.event('Win', {
            category: gameName,
            user: user.id,
            triesForWin: gameData.currentGame.cardTries.length.toString()
        });
        gameData.currentGame.win = true
        addToHistoryAndStatistics(gameData.currentGame)
        setUser(user)
    }
    const nextDay = () => {
        addToHistoryAndStatistics(gameData.currentGame)
        gameData.currentGame = {
            date: getToday(),
            cardTries: [],
            win: false
        }
        setUser(user)
    }
    const addToHistoryAndStatistics = (status: ClassicGameStatus) => {
        addToHistory(status)
        updateStatistics()
        setUser(user)
    }
    const addToHistory = (status: ClassicGameStatus) => {
        gameData.gameHistory = gameData.gameHistory.filter(game => game && game.date != null)
        if (status?.date == null) {
            return
        }
        delete gameData.gameHistory[gameData.gameHistory
            .findIndex(game => sameDay(new Date(game.date), new Date(status.date)))]
        gameData.gameHistory.push({
            date: status.date,
            tries: status.cardTries.length,
            win: status.win
        })
    }

    const updateStatistics = () => {
        const totalGames = gameData.gameHistory.length
        const totalWins = gameData.gameHistory.filter(game => game.win).length
        const currentStreak = gameData.currentGame.win ? gameData.statistics.currentStreak + 1 : 0
        const longestStreak = Math.max(gameData.statistics.longestStreak, currentStreak)
        const allTries = gameData.gameHistory.map(h => h.tries).reduce((a, b) => a + b, 0);
        const averageTries = gameData.gameHistory.length > 0 ? allTries / gameData.gameHistory.length : 0;
        gameData.statistics = {
            totalGames,
            totalWins,
            currentStreak,
            longestStreak,
            averageTries
        }
    }
    const getStatistics = () => {
        return gameData.statistics
    }
    const getGameStatus = () => {
        return {
            ...gameData.currentGame,
            getTriesOfUser: (cards: Card[]) => cards
                .filter(card => gameData.currentGame.cardTries.includes(card.id)).reverse()
        }
    }

    if (!sameDay(new Date(gameData.currentGame.date), getToday())) {
        nextDay()
    }

    return {
        addTry,
        addTryAndWin,
        nextDay,
        getStatistics,
        getGameStatus
    }

}

export const UserProvider: React.FC<UserProviderProps> = props => {
    const { children } = props
    const [user, setUser] = React.useState<User | undefined>(getUser())
    const [language, setLanguageState] = React.useState<Language | undefined>(undefined)

    const setAndUpdateUser = (user: User) => {
        updateUser(user)
        setUser(user)
    }

    // Init User data if needed
    const classicGameController = React.useCallback(() => {
        if (user == null) {
            return undefined
        }
        return generateClassicGameController(user, user.gameData.classic, setAndUpdateUser)
    }, [user])

    React.useEffect(() => {
        if (user == null) {
            return undefined
        }
        updateDefaultLanguage()
    }, [user])
    
    const updateDefaultLanguage = () => {
        if (user?.language) {
            setLanguageState(user.language)
        } else if (user) {
            setLanguage(getLanguageFromNavigator())
        }
    }

    const setLanguage = (language: Language) => {
        if (user == null) {
            return
        }
        user.language = language
        setAndUpdateUser(user)
        setLanguageState(language)
    }

    return (
        <UserContext.Provider
            value={{
                user,
                getClassicGameController: classicGameController,
                getStatsGameController: () => undefined,
                language,
                setLanguage
            }}
        >
            <>
                {children}
            </>
        </UserContext.Provider>
    )
}
