import React from 'react'
import ReactGA from 'react-ga4'
import { useUserContext } from '../user/user.contex';

export const useGAController = (category?: string)=> {
    const { user } = useUserContext()

    const doInteraction = React.useCallback((event: string, data?: {[col: string]: string}) => {
        const userId = user?.id ?? 'Annonymous'
        ReactGA.event(event, {
            category: category,
            user: userId,
            ...(data??{})
        });
    }, [user])

    return {
        doInteraction
    }
}