import React from 'react'
import { Modal } from 'react-bootstrap'
import '../../resources/styles/feedback.css'
import { useMediaQuery } from 'react-responsive'

export type FeedbackPopupProps = {
    show: boolean
    setShow: (show: boolean) => void
}

export const FeedbackPopup: React.FC<FeedbackPopupProps> = (props) => {
    const { show, setShow } = props
    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })

    const widthIframe = isTabletOrMobile ? '100%' : '640'
    const heightIframe = isTabletOrMobile ? '500' : '500'

    const customStyle = isTabletOrMobile ? {"left": "2%"} : { "left": "-4%" }


    const handleClose = () => setShow(false);
    return <Modal show={show} onHide={handleClose} className='feedback-popup' style={customStyle} contentClassName='feedback-popup-content'>
        <Modal.Header closeButton>
        </Modal.Header>
        <Modal.Body  >
            <iframe src="https://docs.google.com/forms/d/e/1FAIpQLSdmKvxosEuFgKl0ZdNgpT0f_nXKMuyrfzDIPXdH18F7iRA4Hw/viewform?embedded=true"
                width={widthIframe} height={heightIframe}>Cargando</iframe>
        </Modal.Body>
    </Modal>
}
