import React from 'react'
import { GameStatistics, GameStatus, User } from '../../types/user'
import { Card } from '../../types/card'
import { Language } from '../../i18n/i18n'
import { StatsCards } from '../../types/stats'


export interface ClassicGameController extends GameController {
  addTry: (cardId: string) => void
  addTryAndWin: (cardId: string) => void
  getGameStatus: () => GameStatus & { getTriesOfUser: (cards: Card[]) => Card[] }
} 

export interface StatsGameController extends GameController {
  addTry: (cardList: string[]) => void
  addTryAndWin: (cardList: string[]) => void
  getGameStatus: () => GameStatus & { getTriesOfUser: () => StatsCards[] }
} 

export interface GameController {
  nextDay: () => void
  getGameStatus: () => GameStatus
  getStatistics: () => GameStatistics
}

interface UserContext {
  user: User | undefined
  getClassicGameController: () => ClassicGameController | undefined
  getStatsGameController: () => StatsGameController | undefined
  language: Language | undefined
  setLanguage: (language: Language) => void
}

export const UserContext = React.createContext<UserContext>({
  user: undefined,
  getClassicGameController: () => undefined,
  getStatsGameController: () => undefined,
  language: undefined,
  setLanguage: (language) => {}
})

export const useUserContext = () => React.useContext(UserContext)
