import cardOfTheDay from '../../../resources/data/cardOfTheDay.json'
import { Card } from '../../../types/card'
import { getToday } from '../../user/utils'

import jsonData from '../../../resources/data/cards.json'

const startDate = new Date(cardOfTheDay.startDate)
const list= cardOfTheDay.list

export const getCardOfTheDay: (cards: Card[]) => Card | undefined = (cards) => {
    const days = getDayNumber()
    
    return cards.find((el) => el.id==list[days % list.length])
}
export const getDayNumber = () => {
    return Math.floor((getToday().getTime() - startDate.getTime()) / (1000 * 60 * 60 * 24))
}

export const whenIsNextCardOfTheDay = () => {
    const next = new Date()
    next.setDate(next.getDate() + 1)
    next.setHours(0, 0, 0, 0)
    return next
}

export const getUpdateDate = () => {
    return cardOfTheDay.updateDate
}

export const getAllCards = () => {
    return (jsonData as any).filter((card: Card) => new Date(card.releaseDate) <= new Date()) as Card[]
}