import React from 'react'
import { CardStatsManager, getCardStatsManager } from './card-manager'
import { StatsGameContext } from './stats-game.context'
import { StatsCards, StatsRange } from '../../../types/stats'


interface UserProviderProps {
    children: React.ReactNode
}

export const StatsGameProvider: React.FC<UserProviderProps> = props => {
    const { children } = props

    const [cardStatsManager, setCardStatsManager] = React.useState<CardStatsManager>({
        getRange: () => undefined,
        getDayNumber: () => 0,
        getStatsOfTheDay: () => ({}),
        getStatsOfTheDayByNumber: () => ({}),
        whenIsNextCardOfTheDay: () => new Date(),
        getUpdateDate: () => new Date()
    })


    return (
        <StatsGameContext.Provider
            value={{
                cardStatsManager,
                setCardStatsManager
            }}
        >
            <>
                {children}
            </>
        </StatsGameContext.Provider>
    )
}
