import React from 'react'
import { CardStatsManager } from './card-manager'
import { StatsCards, StatsRange } from '../../../types/stats'

interface StatsGameContext {
  cardStatsManager: CardStatsManager
  setCardStatsManager: React.Dispatch<React.SetStateAction<CardStatsManager>>
}

export const StatsGameContext = React.createContext<StatsGameContext>({
  cardStatsManager: {
    getRange: () => undefined,
    getDayNumber: () => 0,
    getStatsOfTheDay: () => ({}),
    getStatsOfTheDayByNumber: () => ({}),
    whenIsNextCardOfTheDay: () => new Date(),
    getUpdateDate: () => new Date()
  },
  setCardStatsManager: () => { }
})

export const useStatsGameContext = () => React.useContext(StatsGameContext)
