import React from 'react'
import { HTML5Backend } from 'react-dnd-html5-backend'

import { Card } from './card'
import { CardHolder } from './card-holder'
import { DndProvider, DragSourceMonitor } from 'react-dnd'
import { Stats, StatsCards } from '../../../../types/stats'

interface CardHolderState {
  stats: Stats
  card: string | null
}

interface CardState {
  name: string
}

type DnDStatsProps = {
  cardStats: StatsCards
  onChange?: (wr: StatsCards) => void
  onClean?: React.MutableRefObject<() => void>
  onUpdateCardStats?: React.MutableRefObject<(cardStats: StatsCards) => void>
}

const toCardHolder = (statsCards: StatsCards) => {
  return Object.values(statsCards).map((stat) => ({
    stats: stat,
    card: null
  })).sort((hld1, hld2) => (hld1.stats.games??0) - (hld2.stats.games??0))
}

const toCard = (statsCards: StatsCards) => {
  return Object.keys(statsCards).map((name) => ({
    name,
    type: name
  }))
}

export const DnDStats: React.FC<DnDStatsProps> = React.memo((props) => {
  const { cardStats, onChange, onClean, onUpdateCardStats } = props
  const all = Object.keys(cardStats)

  const [cardHolders, setCardHolders] = React.useState<CardHolderState[]>(toCardHolder(cardStats))
  const [cards, setCards] = React.useState<CardState[]>(toCard(cardStats))

  const cleanAll = React.useCallback(() => {
    setCardHolders(cardHolders.map(({ stats }) => ({ stats, card: null })))
  }, [cardHolders, setCardHolders])

  const updateCardStats = React.useCallback((cardStats: StatsCards) => {
    setCardHolders(toCardHolder(cardStats))
    setCards(toCard(cardStats))
  }, [setCardHolders])

  React.useEffect(() => {
    if (onClean) {
      onClean.current = cleanAll
    }
  }, [cleanAll])

  React.useEffect(() => {
    if (onUpdateCardStats) {
      onUpdateCardStats.current = updateCardStats
    }
  }, [updateCardStats])



  function isDropped(boxName: string) {
    return cardHolders.some(cardHolder => cardHolder.card === boxName)
  }

  const handleDrop = React.useCallback(
    (index: number, item: { name: string }) => {
      const { name } = item

      const newContainer = cardHolders.findIndex((chold, i) => i == index)
      const newContainerCard = cardHolders[newContainer].card
      const oldContainer = cardHolders.findIndex((chold, i) => chold.card == name && i != index)
      setCardHolders(
        cardHolders.map((chold, i) =>
        (newContainer == i ? { stats: chold.stats, card: name } :
          oldContainer == i ? { stats: chold.stats, card: newContainerCard } : chold))
      );
    },
    [cardHolders],
  )
  React.useEffect(() => {
    cardHolders.every(({ card }) => card !== null) && onChange
      && onChange(cardHolders.reduce((acc, { card, stats }) => {
        if (card) {
          return { ...acc, [card]: stats }
        }
        return acc
      }, {}))
  }, [cardHolders])

  const onDropCard = React.useCallback((item: {name: string}, element: DragSourceMonitor) => {
    const result=element.getDropResult()
    if(result==null) {
      console.log("DROP OUT",item)
      setCardHolders(
        cardHolders.map((chold, i) =>
          chold.card == item.name ? { stats: chold.stats, card: null } : chold)
      );
    }
  }, [cardHolders, setCardHolders])
  const onDropCardRef = React.useRef(onDropCard)

  React.useEffect(() => {
    onDropCardRef.current = onDropCard
  }, [onDropCard])



  return (
    <DndProvider backend={HTML5Backend}>
      <div style={{ overflow: 'hidden', clear: 'both' }}>
        {cardHolders.map(({ card, stats }, index) => (
          <CardHolder
            stats={stats}
            accept={all}
            card={card}
            onDrop={(item) => handleDrop(index, item)}
            onDropCardRef={onDropCardRef}
            key={index}
          />
        ))}
      </div>

      <div style={{ overflow: 'hidden', clear: 'both' }} className='d-inline'>
        {cards.filter((card) => !isDropped(card.name))
          .map(({ name }, index) => (
            <Card
              name={name}
              key={index}
              onDropRef={onDropCardRef}
            />
          ))}
      </div>
    </DndProvider>
  )
})
