import React from 'react'
import { DragSourceMonitor, useDrag } from 'react-dnd'

const style: React.CSSProperties = {
  cursor: 'move',
  display: 'inline-block',
}

export interface CardProps {
  name: string,
  onDropRef?: React.MutableRefObject<(item: {name: string;}, element: DragSourceMonitor) => void>
}

export const Card: React.FC<CardProps> =({ name, onDropRef }) => {
  const [{ opacity }, drag] = useDrag(
    () => ({
      end: (el, r) => onDropRef?.current(el, r),
      type: name,
      item: { name },
      collect: (monitor) => ({
        opacity: monitor.isDragging() ? 0.4 : 1,
      }),
    }),
    [name],
  )

  return (
    <div ref={drag} style={{ ...style, opacity }} data-testid="box">
      <img src={require(`../../../../resources/cards/${name}.png`)}
        style={{
          width: "140px",
          height: "140px",
        }}></img>
    </div>
  )
}
