import { getToday } from '../../user/utils'

import stats6080 from '../../../resources/data/stats60-80.json'
import stats80100 from '../../../resources/data/stats80-100.json'
import stats1001000 from '../../../resources/data/stats100-1000.json'
import { StatsCards, StatsFile, StatsRange } from '../../../types/stats'
import image6080 from '../../../resources/images/ranks/6080.jpeg'
import image80100 from '../../../resources/images/ranks/80100.jpeg'
import image1001000 from '../../../resources/images/ranks/1001000.jpeg'

const defaultRange=StatsRange.range80To100
const rangeStats: { [key in StatsRange]: StatsFile } = {
    [StatsRange.range60To80]: stats6080 as any,
    [StatsRange.range80To100]: stats80100 as any,
    [StatsRange.range100To1000]: stats1001000 as any
}
export const rangeImages: { [key in StatsRange]: any } = {
    [StatsRange.range60To80]: image6080,
    [StatsRange.range80To100]: image80100,
    [StatsRange.range100To1000]: image1001000
}

export type CardStatsManager = {
    getRange: () => StatsRange | undefined
    getStatsOfTheDay: () => StatsCards
    getStatsOfTheDayByNumber: (days: number) => StatsCards
    getDayNumber: () => number
    whenIsNextCardOfTheDay: () => Date
    getUpdateDate: () => Date
}
export const getCardStatsManager: (range?: StatsRange) => CardStatsManager = (range) => {
    const ret= {
        getRange: () => range,
        getStatsOfTheDay: () => getStatsOfTheDay(range),
        getStatsOfTheDayByNumber: (days: number) => getStatsOfTheDayByNumber(days, range),
        getDayNumber: () => getDayNumber(range),
        whenIsNextCardOfTheDay: () => whenIsNextCardOfTheDay(),
        getUpdateDate: () => getUpdateDate(range)
    }

    return ret
}


const getStatsOfTheDay = (range: StatsRange | undefined = defaultRange) => {
    const days = getDayNumber(range)
    return getStatsOfTheDayByNumber(days, range)
}
const getStatsOfTheDayByNumber = (days: number, range: StatsRange | undefined = defaultRange) => {
    const list = rangeStats[range].list
    return (list[days % list.length] as any) as StatsCards
}

const toStatsCard = (currentStats: StatsCards) => {
    return Object.keys(currentStats).reduce((acc, key) => {
        return { ...acc, [key]: currentStats[key] }
    }, {} as StatsCards)
}

const getDayNumber = (range: StatsRange | undefined = defaultRange) => {
    return Math.max(0, Math.floor((getToday().getTime() 
                - new Date(rangeStats[range].startDate).getTime()) / (1000 * 60 * 60 * 24)))
}

const whenIsNextCardOfTheDay = () => {
    const next = new Date()
    next.setDate(next.getDate() + 1)
    next.setHours(0, 0, 0, 0)
    return next
}

const getUpdateDate = (range: StatsRange | undefined = defaultRange) => {
    const stats = rangeStats[range]
    return stats.updateDate
}