import React from "react";
// We use Route in order to define the different routes of our application
import { Route, Routes } from "react-router-dom";
import { UserProvider } from "./components/user/user.provider";
import { ClassicGame } from "./components/game/classic/game";
import 'bootstrap/dist/css/bootstrap.min.css';
import './i18n/i18n';
import { SocialMediaBar } from "./components/rrss/social-media-bar";
import { FeedbackProvider } from "./components/feedback/feedback.provider";
import { LanguageSelector } from "./components/language/language-selector";
import ReactGA from 'react-ga4';
import CookieConsent, { OPTIONS } from "react-cookie-consent";
import { useTranslation } from "react-i18next";
import { snapdleNameSpace } from "./i18n/resources";
import './resources/styles/global.css'
import { StatsGame } from "./components/game/stats/game";

const Cookies: React.FC<{ acceptByDefault?: boolean, hide?: boolean }> = (props) => {
  const { t } = useTranslation(snapdleNameSpace)
  const { acceptByDefault = false, hide = false } = props || {}

  React.useEffect(() => {
    ReactGA.gtag('consent', 'default', {
      ad_storage: 'denied',
      ad_user_data: 'denied',
      analytics_storage: acceptByDefault ? 'granted' : 'denied',
      personalization_storage: 'denied',
      functionality_storage: 'denied',
      security_storage: 'granted',
      wait_for_update: 2000,
    })
    ReactGA.initialize('G-DQQ20DKF91');
    ReactGA.send({ hitType: 'pageview', page: window.location.pathname });
  }, [])

  return !hide ? <CookieConsent
    disableStyles={true}
    location={OPTIONS.BOTTOM}
    buttonClasses="btn btn-primary"
    containerClasses="alert alert-warning col-lg-12 cookie-consent"
    contentClasses="text-capitalize"
    buttonText={t('common.cookieConsent.accept')}
    onAccept={() => {
      if (!acceptByDefault) {
        ReactGA.gtag('consent', 'default', {
          analytics_storage: 'granted',
        })
      }
    }}
  >
    <span>{t('common.cookieConsent.text')}</span>
  </CookieConsent> : <></>
}
const App = () => {
  return (
    // redirect to url
    <>
      <UserProvider>
        <FeedbackProvider>
          <Routes>
            <Route path="/" element={<ClassicGame />} />
            <Route path="/demo" element={<StatsGame />} />
          </Routes>

          <SocialMediaBar />
          <LanguageSelector />
        </FeedbackProvider>
      </UserProvider>
      <Cookies acceptByDefault={true} hide={true}></Cookies>
    </>
  );
};
export default App;