import i18next, { i18n as i18nInstance } from 'i18next'
import { initReactI18next } from 'react-i18next'
import { snapdleResources, snapdleNameSpace } from './resources'

export enum Language {
  es = 'es',
  en = 'en',
}

const defaultLanguage = Language.es

export const getLanguageFromNavigator = (): Language => {
  const language = navigator.language
  return language.includes('es') ? Language.es : Language.en

}

const resources = {
  [Language.es]: {
    ...snapdleResources.es,
  },
  [Language.en]: {
    ...snapdleResources.en,
  },
}

const createI18n = (language: string): i18nInstance => {
  const i18n = i18next.createInstance().use(initReactI18next)

  i18n.init({
    resources,
    lng: language,
    fallbackLng: language,
    ns: snapdleNameSpace,
    keySeparator: false,
  })

  return i18n
}

export const i18n = createI18n(defaultLanguage)
