import React from 'react'
import { useTranslation } from 'react-i18next'
import { snapdleNameSpace } from '../../../i18n/resources'
import { DnDStats } from './dnd/container'
import { StatsCards } from '../../../types/stats'
import { useStatsGameContext } from './stats-game.context'

type CardSelectorProps = {
    stats: StatsCards
    onSend: (cards: StatsCards) => void
}

export const StatsCardSelector: React.FC<CardSelectorProps> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { onSend, stats } = props
    const [statsCards, setStatsCards] = React.useState<StatsCards>(stats)
    const onUpdateCardStats = React.useRef<(cardStats: StatsCards) => void>(() => { })
    const onClean = React.useRef<() => void>(() => { })


    React.useEffect(() => {
        onUpdateCardStats.current(statsCards)
    }, [statsCards])

    const [oks, setOks] = React.useState<number>(0)

    const onChangeFunc = React.useCallback((propose: StatsCards) => {
        setOks(Object.keys(statsCards).filter((key) => propose[key] == statsCards[key]).length)

    }, [statsCards, setOks])


    return <>
        <DnDStats cardStats={statsCards} onChange={onChangeFunc} onUpdateCardStats={onUpdateCardStats} />
        OKS: {oks}
    </>
}