import React from 'react'
import { useTranslation } from 'react-i18next'
import { useUserContext } from '../user/user.contex'
import { Language } from '../../i18n/i18n'
import Select from 'react-select'
import { OverlayTrigger, Tooltip } from 'react-bootstrap'

const LanguageTooltipOverlay: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const { t } = useTranslation()
    const tooltip = (<Tooltip>{t('language.selector.tooltip')}</Tooltip>);
    return (<OverlayTrigger placement="left" overlay={tooltip}>
        <div>{children}</div>
    </OverlayTrigger>)
}

export const LanguageSelector: React.FC = () => {
    const { i18n, t } = useTranslation()
    const { language, setLanguage } = useUserContext()

    React.useEffect(() => {
        i18n.changeLanguage(language)
    }, [language])

    const options = Object.keys(Language).map((key) => {
        return { value: key as keyof typeof Language, label: t(`language.short.${key}`) }
    })
    const selectedOption = options.find((option) => option.value === language) ?? options[0]

    return (
        <div className='language-selector-container'>
            <div className='language-selector'>
                
                    <Select
                        value={selectedOption}
                        onChange={(option) => option && setLanguage(Language[option.value])}
                        options={options}
                        menuPlacement='top'
                        components={{SelectContainer: ({ children }) => 
                            <LanguageTooltipOverlay>{children}</LanguageTooltipOverlay>}}
                    ></Select>
                
            </div>
        </div>
    )
}