import React from 'react'
import { FeedbackContext } from './feedback.context'
import { FeedbackPopup } from './feedback-popup'

export const FeedbackProvider: React.FC<{ children: React.ReactNode }> = props => {
    const { children } = props
    const [show, setShow] = React.useState(false)
    const sendFeedbackNotificationRef = React.useRef(() => {})

    return (
        <FeedbackContext.Provider
            value={{
                feedbackShow: show,
                setFeedbackShow: setShow,
                sendFeedbackNotificationRef
            }}
        >
            <>
                <FeedbackPopup {...{show, setShow}}></FeedbackPopup>
                {children}
            </>
        </FeedbackContext.Provider>
    )
}
