import React from 'react'
import { Modal } from 'react-bootstrap'
import { useMediaQuery } from 'react-responsive'

export type GamePopupProps = {
    show: boolean
    setShow: (show: boolean) => void
}

export const GamePopup: React.FC<GamePopupProps & { children: React.ReactNode}> = (props) => {
    const { show, setShow, children } = props

    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' })

    const widthIframe = isTabletOrMobile ? '100%' : '640'
    const heightIframe = isTabletOrMobile ? '500' : '500'


    const close = () => { setShow(false) };
    return <Modal show={show} onHide={close} className='share-result-popup marvel-text'
        contentClassName='share-result-content'>
        <Modal.Header closeButton>
        </Modal.Header>
        <Modal.Body >
            <div style={{ minWidth: widthIframe, minHeight: heightIframe }}>
                {children}
            </div>
        </Modal.Body>
    </Modal>
}