import React from 'react'
import Select from 'react-select'
import { Card } from '../../../types/card'
import { useTranslation } from 'react-i18next'
import { snapdleNameSpace } from '../../../i18n/resources'

type CardSelectorProps = {
    cards: Card[]
    onChange: (card: Card) => void
}

export const CardSelector: React.FC<CardSelectorProps> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)

    const { cards, onChange } = props
    const options = cards.map(card => ({
        value: card.id,
        label: card.name
    })).sort(
        (a, b) => a.label.localeCompare(b.label)
    )

    return <Select options={options} placeholder={t('game.classic.selectCard')}
        onChange={(value) => {
            const card = cards.find(card => card.id === value?.value)
            card && onChange(card)

        }} value={null} className='cardSelector' openMenuOnClick={false}
         styles={{
            valueContainer: (base, state) => ({
               ...base,
               justifyContent: "center"
             })}}></Select>
}