import React from "react";
import { Card } from "../../../types/card";
import { clsx } from 'clsx';
import { snapdleNameSpace } from "../../../i18n/resources";
import { useTranslation } from "react-i18next";
import { useTransition, animated, config } from "@react-spring/web";
import { ComparationResult } from "../../../types/statistics";

type CardTryTableProps = {
    cards: Card[]
    cardOfTheDay: Card
}



export const CardTryTable: React.FC<CardTryTableProps> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { cards, cardOfTheDay } = props
    return <div className='flex-column solution-table-container '>
        <table className="table table-responsive solution-table ">
            <thead>
                <tr>
                    <th scope="col">{t('game.classic.cardName')}</th>
                    <th scope="col">{t('game.classic.cardAbilities')}</th>
                    <th scope="col">{t('game.classic.cardCategories')}</th>
                    <th scope="col">{t('game.classic.cardCost')}</th>
                    <th scope="col">{t('game.classic.cardPower')}</th>
                    <th scope="col">{t('game.classic.cardSource')}</th>
                    <th scope="col">{t('game.classic.cardReleaseDate')}</th>
                </tr>
            </thead>
            <tbody>
                <CardTryList cards={cards} cardOfTheDay={cardOfTheDay}></CardTryList>
            </tbody>
        </table>
    </div>
}

type CardTryListProps = {
    cards: Card[]
    cardOfTheDay: Card
}
const CardTryList: React.FC<CardTryListProps> = (props) => {
    const { cards, cardOfTheDay } = props
    const transitions = useTransition(cards,
        {
            from: { opacity: 0, config: config.molasses },
            enter: { opacity: 1, config: config.molasses },
            leave: { opacity: 0, config: config.molasses },
            keys: card => card.id
        }

    )

    return transitions((style, card) => {
        return <animated.tr style={style} key={card.id}  className="table-row-tr">
            <CardTry tryCard={card} card={cardOfTheDay}></CardTry>
        </animated.tr>

    }
    )
}

type CardTryProps = {
    tryCard: Card
    card: Card
}
const DateFormmat: React.FC<{ dateString?: Date, response: ComparationResult }> = (props) => {
    const { dateString, response } = props
    const { t } = useTranslation(snapdleNameSpace)
    if (!dateString) {
        return <>{t('game.classic.unkown')}</>
    }
    const date = new Date(dateString)
    const month = date.getUTCMonth() + 1; // months from 1-12
    const year = date.getUTCFullYear();
    return <>
        <span >
            {month < 10 ? '0' : ''}{month + '/'}</span>
        <span className={response == ComparationResult.WARNNING ? 'text-option-ok' : ''}>
            {year}</span></>


}

const CardTry: React.FC<CardTryProps> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { tryCard, card } = props

    return <>
        <GenericColumnCardTry tryValue={tryCard.name} value={card.name}></GenericColumnCardTry>
        <GenericColumnCardTryList tryValue={tryCard.abilities} value={card.abilities}></GenericColumnCardTryList>
        <GenericColumnCardTryList tryValue={tryCard.categories} value={card.categories}></GenericColumnCardTryList>
        <GenericColumnCardTry tryValue={tryCard.cost} value={card.cost}></GenericColumnCardTry>
        <GenericColumnCardTry tryValue={tryCard.power} value={card.power}></GenericColumnCardTry>
        <GenericColumnCardTry tryValue={t(tryCard.source)} value={t(card.source)}></GenericColumnCardTry>
        <GenericColumnCardTryDate tryValue={tryCard.releaseDate} value={card.releaseDate}></GenericColumnCardTryDate>
    </>
}

const ThOption: React.FC<{ response: ComparationResult, children: React.ReactNode }> = (props) => {
    const { response, children } = props
    return <th className={classByResponseType(response) + " align-middle table-option-th"}>
        <div className="table-option-container">

            <h1 className="table-option table-option-background">
            </h1>
            <div className="table-option table-option-text-container align-middle">
                <div className="table-option-text">
                    {children}
                </div>
            </div>
        </div>
        </th>
}

const GenericColumnCardTry: React.FC<{ tryValue: any, value: any }> = (props) => {
    const { tryValue, value } = props
    return <ThOption response={simpleCompare(tryValue, value)}>{tryValue}</ThOption>
}

const ConcatElement: React.FC<{ calculateOks: { value: string, isOk: boolean }[] }> = (props) => {
    const { t } = useTranslation(snapdleNameSpace)
    const { calculateOks } = props
    const allOk = calculateOks.every(el => el.isOk)
    const anyOk = calculateOks.some(el => el.isOk)

    return <>{calculateOks.map(
        (element, index) => <div key={element.value} className={anyOk && !allOk && element.isOk ? 'text-option-ok' : ''}>
            {t(element.value)}{index < calculateOks.length - 1 ? <hr className="hr-try-data" /> : <></>}</div >)
    }</>;
}

const GenericColumnCardTryList: React.FC<{ tryValue: any[], value: any[] }> = (props) => {
    const { tryValue, value } = props

    const calculateOks = tryValue.map(
        el => { return { value: el, isOk: value.includes(el) } }
    )

    const oksNumber = calculateOks.filter(el => el.isOk).length

    const response: ComparationResult = oksNumber == value.length && tryValue.length == value.length ? ComparationResult.OK :
        oksNumber > 0 ? ComparationResult.WARNNING : ComparationResult.KO

    return <ThOption response={response} >
        {calculateOks ?
            <ConcatElement calculateOks={calculateOks} /> : <></>}
    </ThOption>
}


const GenericColumnCardTryDate: React.FC<{ tryValue: Date, value: Date }> = (props) => {
    const { tryValue, value } = props
    const response: ComparationResult = calculateColorDate(tryValue, value)
    return <ThOption response={response}>
        <DateFormmat dateString={tryValue} response={response}></DateFormmat>
    </ThOption>
}

const classByResponseType: (response: ComparationResult) => string = (response) => {
    return clsx({
        'bg-success table-option-ok': response == ComparationResult.OK,
        'bg-warning table-option-partial': response == ComparationResult.WARNNING,
        'bg-danger table-option-ko': response == ComparationResult.KO
    })
}

const calculateColorDate: (tryCardDate: Date, cardDate: Date) => ComparationResult = (tryCard, card) => {
    const tryCardDate = tryCard ? new Date(tryCard) : undefined
    const cardDate = card ? new Date(card) : undefined
    if (tryCardDate == undefined || cardDate == undefined) {
        return tryCardDate == cardDate ? ComparationResult.OK : ComparationResult.KO
    }
    if (tryCardDate.getFullYear() != cardDate.getFullYear()) {
        return ComparationResult.KO
    } else if (tryCardDate.getMonth() != cardDate.getMonth()) {
        return ComparationResult.WARNNING
    } else {
        return ComparationResult.OK
    }
}

const simpleCompare: (tryCard: any, card: any) => ComparationResult = (tryCard, card) => {
    return tryCard == card ? ComparationResult.OK : ComparationResult.KO
}

const calculateColor: (tryCard: any, card: any) => string = (tryCard, card) => {
    const response = simpleCompare(tryCard, card)

    return classByResponseType(response)
}